@use 'assets/scss/normalize';

body {
    font-family: Arial, Helvetica, sans-serif;
}

.header {
    display: flex;
    align-items: center;
    padding: 20px 70px 20px 32px;
}

.menu-button {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #fff;
    width: 20px;
    height: 17px;
    margin-right: 28px;
}

.menu-line {
    width: 20px;
    height: 3px;
    background: #1f2022;
    border-radius: 20px;
}

.logo {
    margin-right: 59px;
}

.input-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 830px;
    width: 50%;
    padding-right: 17px;
    background: #ebebeb;
    border-radius: 22px;
}

.input-search {
    width: 100%;
    padding: 12px 23px;
    background-color: transparent;
    border: none;
}

.button-search {
    width: 19px;
    height: 19px;

    background-color: transparent;
}

.icons {
    display: flex;
    align-items: center;
    margin-left: auto;
}

.icon-search {
    fill: #000;
}

.icons-link {
    display: inline;
    opacity: 0.24;
}

.icons-link:hover {
    opacity: 0.7;
}

.icons-link:not(:last-child) {
    margin-right: 50px;
}

.user {
    margin-left: 45px;
}

.user-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
